@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&display=swap");
:root {
  --main:#E2B024;
  --black-grey:#18191A;
  --black: #000;
  --container:1170px;
  --Poppins:Poppins,sans-serif;
}

@font-face {
  font-family: "Chocolate";
  src: url("../fonts/Chocolate-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

