html {
  box-sizing: border-box;
  font-family: sans-serif;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  outline: none;
}

body {
  font-family: "Chocolate";
  font-weight: 400;
  line-height: 1.4;
  margin: 0;
  font-size: 15px;
  color: #fff;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  -webkit-text-size-adjust: none;
  cursor: default;
  background: var(--black);
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  display: inline-block;
  color: #fff;
}

p {
  margin: 0;
  font-family: 'Poppins', sans-serif;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

h1,
h2,
h3 {
  margin: 0;
}

.container {
  max-width: var(--container);
  margin: 0 auto;
}

.section_title h2 {
  text-align: center;
  font-size: 48px;
  font-weight: 400;
}

.section_title h2:not([class]) {
  margin: 0;
}

.section_title h2 span {
  color: var(--main);
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.futured-products {
  padding: 150px 0;
}

.futured-products__title {
  margin-bottom: 90px;
}

.futured-products__buttons {
  display: flex;
  justify-content: center;
  border-bottom: 1px solid #fff;
  width: max-content;
  margin: auto;
  padding-bottom: 5px;
  margin-bottom: 90px;
}

.futured-products__button {
  background: transparent;
  border: none;
  color: #fff;
  font-family: var(--Poppins);
  font-size: 24px;
  cursor: pointer;
  position: relative;
  padding: 0;
  transition: all .3s;
}

.futured-products__button:not(:last-child) {
  margin-right: 70px;
}

.futured-products__button:after {
  content: '';
  display: block;
  width: 100%;
  height: 1px;
  background: var(--main);
  position: absolute;
  bottom: -6px;
  opacity: 0;
  transition: all .3s;
}

.futured-products__button.active {
  color: var(--main);
}

.futured-products__button.active:after {
  opacity: 1;
}

.futured-products__button:hover:after {
  opacity: 1;
}

.futured-products__item {
  text-align: center;
}

.futured-products__link {
  display: block;
  width: max-content;
  margin: auto;
  color: var(--main);
  font-size: 18px;
  transition: all .3s;
}

.futured-item {
  width: 25%;
  margin-bottom: 90px;
}

.futured-item__discount {
  position: absolute;
  top: 20px;
  left: 30px;
  font-family: var(--Poppins);
  color: var(--black);
  background: var(--main);
  padding: 10px 16px;
  font-size: 14px;
}

.futured-item__img {
  margin-bottom: 5px;
  min-height: 360px;
}

.futured-item__name {
  font-size: 24px;
  margin-bottom: 10px;
}

.futured-item__price {
  font-family: var(--Poppins);
  color: var(--main);
  font-size: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.futured-item__price span {
  font-size: 16px;
  display: inline-block;
  margin-right: 5px;
}

.client-review {
  background: url("../img/client-review__bg.png");
  padding-top: 124px;
  padding-bottom: 154px;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

.client-review__title {
  margin-bottom: 58px;
}

.client-review__slider {
  margin-bottom: 50px;
}

.client-review__pagination {
  text-align: center;
}

.client-review__pagination .swiper-pagination-bullet {
  width: 20px;
  height: 20px;
  background: #fff;
  opacity: 1;
}

.client-review__pagination .swiper-pagination-bullet:not(:last-child) {
  margin-right: 10px;
}

.client-review__pagination .swiper-pagination-bullet.swiper-pagination-bullet-active {
  background: var(--main);
}

.review-slide {
  text-align: center;
}

.review-slide__img {
  margin-bottom: 30px;
}

.review-slide__name {
  font-size: 24px;
  font-family: var(--Poppins);
  font-weight: 600;
  margin-bottom: 30px;
}

.review-slide__text {
  max-width: 878px;
  margin: auto;
  font-family: var(--Poppins);
}

.amazing-offer {
  padding-top: 162px;
  padding-bottom: 152px;
}

.amazing-offer__title {
  margin-bottom: 100px;
}

.amazing-offer__slider {
  margin-bottom: 88px;
}

.amazing-offer__pagination {
  text-align: center;
}

.amazing-offer__pagination .swiper-pagination-bullet {
  width: 20px;
  height: 20px;
  background: #fff;
  opacity: 1;
}

.amazing-offer__pagination .swiper-pagination-bullet:not(:last-child) {
  margin-right: 10px;
}

.amazing-offer__pagination .swiper-pagination-bullet.swiper-pagination-bullet-active {
  background: var(--main);
}

.amazing-item {
  position: relative;
  transform: scale(0.9);
  transition: all .3s;
}

.amazing-item__about {
  position: absolute;
  left: 25px;
  bottom: 10px;
}

.amazing-item__name {
  font-size: 24px;
}

.amazing-item__price {
  font-family: var(--Poppins);
  font-size: 36px;
  font-weight: 600;
}

.amazing-item.swiper-slide-active {
  transform: scale(1);
}

.featured-events {
  margin-bottom: 150px;
}

.featured-events__title {
  margin-bottom: 58px;
}

.featured-events__container {
  position: relative;
}

.featured-events__next, .featured-events__prev {
  width: 64px;
  height: 64px;
  border-radius: 50%;
  border: 1px solid var(--main);
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 55%;
  cursor: pointer;
}

.featured-events__prev {
  left: -85px;
}

.featured-events__next {
  right: -85px;
}

.featured-events__next svg {
  transform: rotate(-180deg);
}

.events-item {
  display: flex;
  font-family: var(--Poppins);
}

.events-item__about {
  width: 100%;
  max-width: 800px;
  padding: 60px 0 80px 30px;
  background: var(--black-grey);
}

.events-item__name {
  font-weight: 600;
  font-size: 24px;
  margin-bottom: 10px;
}

.events-item__date {
  font-weight: 300;
  font-size: 18px;
  margin-bottom: 44px;
  display: block;
}

.events-item__text {
  font-weight: 300;
  font-size: 18px;
  margin-bottom: 45px;
  max-width: 512px;
}

.events-item__link {
  font-family: "Chocolate";
  font-size: 18px;
  color: var(--main);
  text-transform: uppercase;
}

.contact-us {
  margin-bottom: 150px;
}

.contact-us__title {
  margin-bottom: 95px;
}

.contact-us__block {
  display: flex;
  flex-wrap: wrap;
}

.contact-us__about {
  width: 100%;
  max-width: 466px;
  margin-right: 35px;
}

.contact-us__text {
  font-size: 18px;
  font-family: var(--Poppins);
  font-weight: 300;
  line-height: 2;
  margin-bottom: 90px;
}

.contact-us__phone, .contact-us__mail, .contact-us__address {
  display: flex;
  align-items: center;
  font-family: var(--Poppins);
  margin-bottom: 44px;
  font-size: 18px;
  transition: all .3s ease-out;
}

.contact-us__phone:before, .contact-us__mail:before, .contact-us__address:before {
  content: '';
  display: block;
  width: 24px;
  height: 24px;
  margin-right: 26px;
}

.contact-us__phone:hover, .contact-us__mail:hover, .contact-us__address:hover {
  color: var(--main);
}

.contact-us__address:hover {
  color: #fff;
}

.contact-us__phone:before {
  background: url("../img/Call.svg") no-repeat;
}

.contact-us__mail:before {
  background: url("../img/Message.svg") no-repeat;
}

.contact-us__address {
  margin-bottom: 60px;
}

.contact-us__address:before {
  background: url("../img/Location.svg") no-repeat;
}

.contact-us__soc {
  max-width: 360px;
}

.contact-us__form {
  flex-grow: 1;
}

.contact-us__input {
  width: 100%;
  position: relative;
  margin-bottom: 30px;
}

.contact-us__input input {
  width: 100%;
  height: 60px;
  border: 1px solid #FFFFFF;
  background: transparent;
  color: #fff;
  font-size: 22px;
  padding-left: 45px;
  font-family: var(--Poppins);
}

.contact-us__input label {
  font-family: var(--Poppins);
  font-size: 24px;
  font-weight: 300;
  position: absolute;
  top: -16px;
  left: 35px;
  background: var(--black);
  padding: 0 10px;
}

.contact-us__area {
  position: relative;
  margin-bottom: 30px;
}

.contact-us__area textarea {
  width: 100%;
  height: 132px;
  border: 1px solid #FFFFFF;
  background: transparent;
  color: #fff;
  font-size: 22px;
  padding-left: 45px;
  padding-top: 24px;
  font-family: var(--Poppins);
}

.contact-us__area label {
  font-family: var(--Poppins);
  font-size: 24px;
  font-weight: 300;
  position: absolute;
  top: -16px;
  left: 35px;
  background: var(--black);
  padding: 0 10px;
}

.contact-us__submit {
  font-family: var(--Poppins);
  background: var(--main);
  color: var(--black-grey);
  border: 1px solid transparent;
  width: 100%;
  max-width: 200px;
  height: 82px;
  font-weight: 500;
  font-size: 36px;
  cursor: pointer;
  transition: all .3s ease-out;
}

.contact-us__submit:hover {
  background: transparent;
  border: 1px solid var(--main);
  color: #fff;
}

.soc {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.soc__item {
  width: 48px;
  height: 48px;
  transition: all .3s ease-in-out;
}

.soc__item:hover {
  transform: translateY(-10px);
}

.soc .soc-google {
  background: url("../img/google.svg");
}

.soc .soc-twiter {
  background: url("../img/twitter.svg");
}

.soc .soc-in {
  background: url("../img/linkedin.svg");
}

.soc .soc-facebook {
  background: url("../img/facebook.svg");
}

.newsletter {
  text-align: center;
  background: var(--black-grey);
  padding-top: 52px;
  padding-bottom: 62px;
}

.newsletter__text {
  font-size: 18px;
  margin-bottom: 70px;
}

.newsletter__form {
  width: 100%;
  max-width: 570px;
  margin: auto;
  display: flex;
}

.newsletter__field {
  width: 100%;
  max-width: 446px;
}

.newsletter__field input {
  width: 100%;
  height: 64px;
  background: var(--black);
  border: none;
  font-family: var(--Poppins);
  padding-left: 34px;
  font-size: 18px;
  color: #fff;
}

.newsletter__field input::placeholder {
  color: #fff;
}

.newsletter__submit {
  flex-grow: 1;
  border: none;
  background: var(--main);
  font-size: 18px;
  color: #1F1F1F;
  font-family: var(--Poppins);
  cursor: pointer;
}

.footer {
  padding-top: 132px;
  padding-bottom: 44px;
}

.footer__about {
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 92px;
}

.footer__column {
  width: calc(100% / 4);
}

.footer__soc {
  max-width: 226px;
  margin-bottom: 30px;
}

.footer__soc .soc__item {
  width: 24px;
  height: 24px;
  background-size: cover;
}

.footer__contact-item {
  margin-bottom: 24px;
  font-size: 15px;
}

.footer__contact-item:before {
  width: 18px;
  height: 18px;
  margin-right: 12px;
  background-size: contain;
}

.footer__copyright {
  padding-top: 22px;
  border-top: 1px solid #fff;
  text-align: center;
  font-family: var(--Poppins);
  font-weight: 300;
  font-size: 16px;
}

.column-footer__title {
  color: var(--main);
  font-size: 24px;
  margin-bottom: 42px;
}

.column-footer__menu-item:not(:last-child) {
  margin-bottom: 15px;
}

.column-footer__menu-link {
  font-family: var(--Poppins);
  font-size: 16px;
  transition: color .3s ease-in;
}

.column-footer__menu-link:hover {
  color: var(--main);
}

.top-bar__logan {
  font-size: 12px;
}

.top-bar__links a {
  color: #fff;
  font-family: 'Poppins', sans-serif;
  display: inline-block;
  font-size: 12px;
  transition: all .3s ease-out;
}

.top-bar__links a:not(:last-child) {
  margin-right: 36px;
}

.top-bar__links a:hover {
  color: var(--main);
}

.top-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 0;
}

.top-bar__logo {
  margin: auto;
  margin-right: 110px;
}

.top-bar__about {
  width: 100%;
  max-width: 410px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.top-bar__icons {
  width: 100%;
  max-width: 128px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.top-bar__icon svg {
  width: 24px;
  height: 24px;
}

.top-bar__icon .svg-search,
.top-bar__icon .svg-cart {
  transition: all .3s;
  stroke: #fff;
}

.top-bar__icon.burger {
  width: 18px;
}

.top-bar__icon.burger span {
  display: block;
  width: 100%;
  height: 2px;
  background: #fff;
}

.top-bar__icon.burger span:not(:last-child) {
  margin-bottom: 5px;
}

.top-bar__icon:hover .svg-search,
.top-bar__icon:hover .svg-cart {
  stroke: var(--main);
}

.top-menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 22px 0;
  border-top: 1px solid rgba(255, 255, 255, 0.6);
  border-bottom: 1px solid rgba(255, 255, 255, 0.6);
}

.top-menu a {
  transition: all .3s;
}

.top-menu a:hover {
  color: var(--main);
}

.top-menu a.active {
  color: var(--main);
  border-bottom: 1px solid var(--main);
}

.each-bottle {
  text-align: center;
}

.each-bottle__text {
  font-size: 24px;
  margin-top: 190px;
  margin-bottom: 20px;
  line-height: 1;
}

.each-bottle__title {
  font-size: 96px;
  line-height: 1;
  margin-bottom: 24px;
}

.each-bottle__title span {
  color: var(--main);
}

.each-bottle__small-text {
  font-size: 16px;
  line-height: 34px;
  max-width: 770px;
  margin: auto;
  margin-bottom: 35px;
}

.each-bottle__link {
  font-size: 16px;
  color: var(--main);
  transition: all .3s;
  margin-bottom: 36px;
}

.each-bottle__img {
  line-height: 0;
}

.new-wines {
  background: var(--black-grey);
}

.new-wines .new-wines__slider {
  padding: 195px 0;
}

.new-wines .new-wines__item {
  display: flex;
}

.new-wines .new-wines__text {
  margin-right: 60px;
}

.new-wines .new-wines__text > span {
  font-family: 'Poppins', sans-serif;
  font-size: 20px;
  display: block;
  margin-bottom: 30px;
}

.new-wines .text-block__title {
  font-size: 74px;
  color: var(--main);
  margin-bottom: 12px;
  line-height: 1;
}

.new-wines .text-block__sub-title {
  font-size: 36px;
  margin-bottom: 52px;
  line-height: 1;
}

.new-wines .text-block__paragraph {
  font-size: 18px;
  max-width: 534px;
  margin-bottom: 64px;
  line-height: 2.6;
}

.text-block__links {
  display: flex;
}

.text-block__links a {
  width: 100%;
  max-width: 233px;
  height: 69px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  font-size: 24px;
  font-family: 'Poppins', sans-serif;
}

.text-block__links a.learn-more {
  color: #1F1F1F;
  background: var(--main);
  border: 1px solid transparent;
  margin-right: 60px;
  transition: all .3s ease-out;
}

.text-block__links a.learn-more:hover {
  background: transparent;
  color: #fff;
  border: 1px solid var(--main);
}

.text-block__links a.view-more {
  background: transparent;
  border: 1px solid var(--main);
  transition: all .3s ease-out;
}

.text-block__links a.view-more:hover {
  color: #1F1F1F;
  background: var(--main);
}

.new-wines__image {
  min-width: 585px;
  min-height: 485px;
  border: 10px solid var(--main);
  position: relative;
}

.new-wines__image::before {
  content: '';
  display: block;
  width: 374px;
  height: 374px;
  background: var(--main);
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.new-wines__image .new-wines__image--bottle {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.new-wines__image .swiper-button-prev,
.new-wines__image .swiper-button-next {
  font-family: 'Poppins', sans-serif;
  font-weight: 500;
  font-size: 20px;
  color: #fff;
  border-bottom: 1px solid #fff;
  width: auto;
  height: auto;
  opacity: 1;
}

.new-wines__image .swiper-button-prev::after,
.new-wines__image .swiper-button-next::after {
  display: none;
}

.new-wines__image .swiper-button-prev::before,
.new-wines__image .swiper-button-next::before {
  content: '';
  display: block;
  width: 54px;
  height: 64px;
  position: absolute;
  left: 0;
  background: var(--black-grey);
  z-index: -1;
}

.new-wines__image .swiper-button-next {
  right: -10px;
}

.new-wines__image .swiper-button-prev {
  left: -10px;
}

.animate-link {
  background: linear-gradient(currentColor 0 0) left var(--p, 50%) bottom 0/var(--d, 10%) 2px no-repeat, linear-gradient(currentColor 0 0) right var(--p, 50%) bottom 0/var(--d, 10%) 2px no-repeat;
  transition: 0.3s, background-position 0.3s 0.3s;
}

.animate-link:hover {
  --d: 100%;
  --p: 100%;
  transition: 0.3s, background-size 0.3s 0.3s;
}
