.top-bar__logan {
  font-size: 12px;
}

.top-bar__links {
  a {
    color: #fff;
    font-family: 'Poppins', sans-serif;
    display: inline-block;
    font-size: 12px;
    transition: all .3s ease-out;

    &:not(:last-child) {
      margin-right: 36px;
    }

    &:hover {
      color: var(--main);
    }

  }
}

.top-bar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 12px 0;
}

.top-bar__logo {
  margin: auto;
  margin-right: 110px
}

.top-bar__about {
  width: 100%;
  max-width: 410px;
  display: flex;
  justify-content: space-between;
  align-items: center
}

.top-bar__icons {
  width: 100%;
  max-width: 128px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.top-bar__icon {
  svg {
    width: 24px;
    height: 24px;
  }

  .svg-search,
  .svg-cart{
    transition: all .3s;
    stroke: #fff;
  }

  &.burger {
    width: 18px;

    span {
      display: block;
      width: 100%;
      height: 2px;
      background: #fff;

      &:not(:last-child) {
        margin-bottom: 5px;
      }
    }
  }
  &:hover{
    .svg-search,
    .svg-cart{
      stroke:var(--main);
    }
  }

}

.top-menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 22px 0;
  border-top: 1px solid rgba(255, 255, 255, 0.6);
  border-bottom: 1px solid rgba(255, 255, 255, 0.6);

  a {
    transition: all .3s;

    &:hover {
      color: var(--main);
    }

    &.active {
      color: var(--main);
      border-bottom: 1px solid var(--main);

    }
  }


}

.each-bottle {
  text-align: center;

  &__text {
    font-size: 24px;
    margin-top: 190px;
    margin-bottom: 20px;
    line-height: 1;
  }

  &__title {
    font-size: 96px;
    line-height: 1;
    margin-bottom: 24px;

    span {
      color: var(--main);
    }
  }

  &__small-text {
    font-size: 16px;
    line-height: 34px;
    max-width: 770px;
    margin: auto;
    margin-bottom: 35px;
  }

  &__link {
    font-size: 16px;
    color: var(--main);

    transition: all .3s;
    margin-bottom: 36px;

  }

  &__img {
    line-height: 0;
  }

}

.new-wines {
  background: var(--black-grey);

  .new-wines__slider {
    padding: 195px 0;
  }

  .new-wines__item {
    display: flex;
  }

  .new-wines__text {
    margin-right: 60px;

    > span {
      font-family: 'Poppins', sans-serif;
      font-size: 20px;
      display: block;
      margin-bottom: 30px;
    }
  }

  .text-block__title {
    font-size: 74px;
    color: var(--main);
    margin-bottom: 12px;
    line-height: 1
  }

  .text-block__sub-title {
    font-size: 36px;
    margin-bottom: 52px;
    line-height: 1;
  }

  .text-block__paragraph {
    font-size: 18px;
    max-width: 534px;
    margin-bottom: 64px;
    line-height: 2.6;
  }
}

.text-block__links {
  display: flex;

  a {
    width: 100%;
    max-width: 233px;
    height: 69px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    font-size: 24px;
    font-family: 'Poppins', sans-serif;

    &.learn-more {
      color: #1F1F1F;
      background: var(--main);
      border: 1px solid transparent;
      margin-right: 60px;
      transition: all .3s ease-out;
        &:hover{
          background: transparent;
          color:#fff;
          border: 1px solid var(--main);
        }
    }

    &.view-more {
      background: transparent;
      border: 1px solid var(--main);
      transition: all .3s ease-out;
        &:hover{
          color: #1F1F1F;
          background: var(--main);
        }
    }
  }
}

.new-wines__image {
  min-width: 585px;
  min-height: 485px;
  border: 10px solid var(--main);
  position: relative;

  &::before {
    content: '';
    display: block;
    width: 374px;
    height: 374px;
    background: var(--main);
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .new-wines__image--bottle {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .swiper-button-prev,
  .swiper-button-next {
    font-family: 'Poppins', sans-serif;
    font-weight: 500;
    font-size: 20px;
    color: #fff;
    border-bottom: 1px solid #fff;
    width: auto;
    height: auto;

    opacity: 1;

    &::after {
      display: none;
    }

    &::before {
      content: '';
      display: block;
      width: 54px;
      height: 64px;
      position: absolute;
      left: 0;
      background: var(--black-grey);
      z-index: -1;
    }
  }

  .swiper-button-next {
    right: -10px;
  }

  .swiper-button-prev {
    left: -10px;
  }
}

.animate-link {
  background: linear-gradient(currentColor 0 0) left var(--p, 50%) bottom 0 /
      var(--d, 10%) 2px no-repeat,
  linear-gradient(currentColor 0 0) right var(--p, 50%) bottom 0 /
      var(--d, 10%) 2px no-repeat;
  transition: 0.3s, background-position 0.3s 0.3s;

  &:hover {
    --d: 100%;
    --p: 100%;
    transition: 0.3s, background-size 0.3s 0.3s;
  }
}

