.footer {
  padding-top: 132px;
  padding-bottom: 44px;

  &__about {
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 92px;
  }

  &__column {
    width: calc(100% / 4);
  }

  &__soc {
    max-width: 226px;
    margin-bottom: 30px;

    .soc {
      &__item {
        width: 24px;
        height: 24px;
        background-size: cover;
      }
    }
  }

  &__contact-item {
    margin-bottom: 24px;
    font-size: 15px;

    &:before {
      width: 18px;
      height: 18px;
      margin-right: 12px;
      background-size: contain;
    }
  }
  &__copyright{
    padding-top: 22px;
    border-top: 1px solid #fff;
    text-align: center;
    font-family: var(--Poppins);
    font-weight: 300;
    font-size: 16px;
  }
}

.column-footer {
  &__title {
    color: var(--main);
    font-size: 24px;
    margin-bottom: 42px;
  }

  &__menu-item {
    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }

  &__menu-link {
    font-family: var(--Poppins);
    font-size: 16px;
    transition: color .3s ease-in;
      &:hover{
        color:var(--main);
      }
  }

}
