.newsletter {
  text-align: center;
  background: var(--black-grey);
  padding-top: 52px;
  padding-bottom: 62px;

  &__text {
    font-size: 18px;
    margin-bottom: 70px;
  }
  &__form{
    width: 100%;
    max-width: 570px;
    margin: auto;
    display: flex;
  }
  &__field{
    width: 100%;
    max-width: 446px;
      input{
        width: 100%;
        height: 64px;
        background: var(--black);
        border: none;
        font-family: var(--Poppins);
        padding-left: 34px;
        font-size: 18px;
        color: #fff;
          &::placeholder{
            color: #fff;
          }
      }
  }
  &__submit{
    flex-grow: 1;
    border: none;
    background:var(--main);
    font-size: 18px;
    color: #1F1F1F;
    font-family: var(--Poppins);
    cursor: pointer;
  }
}