.featured-events{
  margin-bottom: 150px;
  &__title{
    margin-bottom: 58px;
  }
  &__container{
    position: relative;
  }
  &__next,
  &__prev{
    width: 64px;
    height: 64px;
    border-radius: 50%;
    border:1px solid var(--main);
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 55%;
    cursor: pointer;
  }
  &__prev{
    left: -85px;
  }
  &__next{
    right: -85px;
      svg{
        transform: rotate(-180deg);
      }
  }
}
.events-item{
  display: flex;
  font-family: var(--Poppins);
  &__about{
    width: 100%;
    max-width: 800px;
    padding: 60px 0 80px 30px;
    background: var(--black-grey);
  }
  &__name{
    font-weight: 600;
    font-size: 24px;
    margin-bottom: 10px;
  }
  &__date{
    font-weight: 300;
    font-size: 18px;
    margin-bottom: 44px;
    display: block;
  }
  &__text{
    font-weight: 300;
    font-size: 18px;
    margin-bottom: 45px;
    max-width: 512px;
  }
  &__link{
    font-family:"Chocolate";
    font-size: 18px;
    color:var(--main);
    text-transform: uppercase;
  }
}