.contact-us {
  margin-bottom: 150px;

  &__title {
    margin-bottom: 95px;
  }

  &__block {
    display: flex;
    flex-wrap: wrap;


  }

  &__about {
    width: 100%;
    max-width: 466px;
    margin-right: 35px;
  }

  &__text {
    font-size: 18px;
    font-family: var(--Poppins);
    font-weight: 300;
    line-height: 2;
    margin-bottom: 90px;
  }

  &__phone,
  &__mail,
  &__address {
    display: flex;
    align-items: center;
    font-family: var(--Poppins);
    margin-bottom: 44px;
    font-size: 18px;
    transition: all .3s ease-out;

    &:before {
      content: '';
      display: block;
      width: 24px;
      height: 24px;
      margin-right: 26px;
    }

    &:hover {
      color: var(--main);
    }
  }
  &__address{
    &:hover{
      color:#fff;
    }
  }

  &__phone {
    &:before {
      background: url("../img/Call.svg") no-repeat;
    }
  }

  &__mail {
    &:before {
      background: url("../img/Message.svg") no-repeat;
    }
  }

  &__address {
    margin-bottom: 60px;

    &:before {
      background: url("../img/Location.svg") no-repeat;
    }
  }

  &__soc {
    max-width: 360px;
  }

  &__form {
    flex-grow: 1;
  }

  &__input {
    width: 100%;
    position: relative;
    margin-bottom: 30px;

    input {
      width: 100%;
      height: 60px;
      border: 1px solid #FFFFFF;
      background: transparent;
      color: #fff;
      font-size: 22px;
      padding-left: 45px;
      font-family: var(--Poppins);
    }

    label {
      font-family: var(--Poppins);
      font-size: 24px;
      font-weight: 300;
      position: absolute;
      top: -16px;
      left: 35px;
      background: var(--black);
      padding: 0 10px;
    }
  }

  &__area {
    position: relative;
    margin-bottom: 30px;

    textarea {
      width: 100%;
      height: 132px;
      border: 1px solid #FFFFFF;
      background: transparent;
      color: #fff;
      font-size: 22px;
      padding-left: 45px;
      padding-top: 24px;
      font-family: var(--Poppins);
    }

    label {
      font-family: var(--Poppins);
      font-size: 24px;
      font-weight: 300;
      position: absolute;
      top: -16px;
      left: 35px;
      background: var(--black);
      padding: 0 10px;
    }
  }

  &__submit {
    font-family: var(--Poppins);
    background: var(--main);
    color: var(--black-grey);
    border: 1px solid transparent;
    width: 100%;
    max-width: 200px;
    height: 82px;
    font-weight: 500;
    font-size: 36px;
    cursor: pointer;
    transition: all .3s ease-out;

    &:hover {
      background: transparent;
      border: 1px solid var(--main);
      color: #fff;
    }
  }
}