@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800&display=swap');
html {
  box-sizing: border-box;
  font-family: sans-serif;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  outline: none;
}

body{
  font-family:"Chocolate";
  font-weight: 400;
  line-height: 1.4;
  margin: 0;
  font-size: 15px;
  color: #fff;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  -webkit-text-size-adjust: none;
  cursor: default;
  background: var(--black);
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
  display: inline-block;
  color:#fff;
}
p{
  margin: 0;
  font-family: 'Poppins', sans-serif;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
h1,
h2,
h3{
  margin: 0;
}
.container{
  max-width: var(--container);
  margin: 0 auto;
}
.section_title{
  h2{
    &:not([class]){
      margin: 0;
    }

    text-align: center;
    font-size: 48px;
    font-weight: 400;
      span{
        color:var(--main);
      }
  }
}
.is-outer {
  position: relative !important; // stylelint-disable-line declaration-no-important
  left: -9999px !important; // stylelint-disable-line declaration-no-important
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  @include stop-scroll;
}

.is-hidden {
  display: none !important; // stylelint-disable-line declaration-no-important
}

.for-mobile {
  display: none;

  @include mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important; // stylelint-disable-line declaration-no-important
}

.visually-hidden {
  @include visually-hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}


.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}
